const Transition = posed.div({
  preEnter: {
    x: 0,
    opacity: 1,
  },
  enter: {
    opacity: 1,
    x: 0,
    transition: {
      // delay: 600,
      duration: 600,
      ease: 'easeInOut'
      },

      // applyAtEnd: { zIndex: 1 },
  },
    exit: { 
      x: 0,
      opacity: 1,
    transition: {
      duration: 600,
      ease: 'easeInOut'
      },
      
  },
})

let Mouse = styled.div`
  position: fixed;
  height: 50px;
  width: 50px;
  z-index: 11000;

  img {
    height: 100%;
    width: 100%
  }
`




